import { WithdrawOneData } from "../types";

export const okxWithdraw = async (data: WithdrawOneData): Promise<void> => {
  try {
    // @ts-ignore
    const okx = new ccxt.okx({
      apiKey: data.apiKey,
      secret: data.secret,
      password: data.passPhrase,
      enableRateLimit: true,
    });

    const chainName = `${data.token}-${data.network}`;

    const fee = await getWithdrawalFee(okx, data.token, data.network);

    await okx.withdraw(data.token, data.amount, data.wallet, {
      toAddress: data.wallet,
      chainName,
      dest: 4,
      fee,
      pwd: "-",
      amt: data.amount,
      network: data.network,
    });

    data.setStatus(
      (prev: string) => `${prev}>>> Success | ${data.wallet} | ${data.amount}\n`
    );
  } catch (error: any) {
    data.setStatus(
      (prev: string) =>
        `${prev}>>> Fail | ${data.wallet} | error: ${error.message}\n`
    );
  }
};

const getWithdrawalFee = async (okx: any, token: string, network: string) => {
  const currencies = await okx.fetchCurrencies();

  try {
    return currencies[token]["networks"][network]["fee"];
  } catch {
    return 0;
  }
};
