import { WithdrawOneData } from "../types";

export const kucoinWithdraw = async (data: WithdrawOneData): Promise<void> => {
  try {
    // @ts-ignore
    const kucoin = new ccxt.kucoin({
      apiKey: data.apiKey,
      secret: data.secret,
      password: data.passPhrase,
      enableRateLimit: true,
    });

    await kucoin.withdraw(data.token, data.amount, data.wallet, {
      network: data.network,
    });

    data.setStatus(
      (prev: string) => `${prev}>>> Success | ${data.wallet} | ${data.amount}\n`
    );
  } catch (error: any) {
    data.setStatus(
      (prev: string) =>
        `${prev}>>> Fail | ${data.wallet} | error: ${error.message}\n`
    );
  }
};
