import { WithdrawOneData } from "../types";

export const gateWithdraw = async (data: WithdrawOneData): Promise<void> => {
  try {
    // @ts-ignore
    const gate = new ccxt.gate({
      apiKey: data.apiKey,
      secret: data.secret,
    });

    await gate.withdraw(data.token, data.amount, data.wallet, {
      network: data.network,
    });

    data.setStatus(
      (prev: string) => `${prev}>>> Success | ${data.wallet} | ${data.amount}\n`
    );
  } catch (error: any) {
    data.setStatus(
      (prev: string) =>
        `${prev}>>> Fail | ${data.wallet} | error: ${error.message}\n`
    );
  }
};
