import React, { useEffect, useState } from "react";
import {
  Button,
  Checkbox,
  Col,
  Form,
  Input,
  Row,
  Select,
  Space,
  Typography,
} from "antd";
import CORSUnblock from "./CORSUnblock";
import { ExchangeMarket } from "../helpers/exchange-market-withdraws/enums";
import { exchangeMarketWithdrawToMany } from "../helpers/exchange-market-withdraws";

interface IBinanceWithdrawProps {
  status?: string;
  setStatus: Function;
}

const BinanceWithdrawForm: React.FC<IBinanceWithdrawProps> = ({
  setStatus,
}) => {
  const [isShowPassPhraseField, setIsShowPassPhraseField] = useState(false);
  const [isCORSDisabled, setIsCORSDisabled] = useState(true);

  useEffect(() => {
    const effect = async () => {
      try {
        await fetch("https://example.com");
        setIsCORSDisabled(true);
      } catch {
        setIsCORSDisabled(false);
      }
    };

    effect();
    const checkCORSInterval = setInterval(effect, 5000);

    return () => {
      clearInterval(checkCORSInterval);
    };
  }, []);

  const [form] = Form.useForm();
  const token = Form.useWatch("token", form);

  const onFinish = (values: any) => {
    const {
      exchangeMarket,
      apiKey,
      secret,
      passPhrase,
      token,
      network,
      isShuffleWallets,
    } = values;

    exchangeMarketWithdrawToMany({
      setStatus,
      exchangeMarket,
      apiKey,
      secret,
      passPhrase,
      token,
      network,
      isShuffleWallets,
      amountFrom: Number(values.amountFrom),
      amountTo: Number(values.amountTo),
      delayFrom: Number(values.delayFrom),
      delayTo: Number(values.delayTo),
      wallets: String(values.wallets).split("\n"),
    });
  };

  const onFinishFailed = (errorInfo: any) => {
    console.log("Failed:", errorInfo);
  };

  const setIsShowPassPhraseFieldIfNeeded = (value: string) => {
    setIsShowPassPhraseField(
      [String(ExchangeMarket.OKX), String(ExchangeMarket.KUCOIN)].includes(
        value
      )
    );
  };

  return (
    <Form
      form={form}
      layout="vertical"
      onFinish={onFinish}
      onFinishFailed={onFinishFailed}
      initialValues={{
        exchangeMarket: ExchangeMarket.BINANCE,
        isShuffleWallets: false,
      }}
    >
      <Row gutter={30}>
        <Col span={12}>
          <Form.Item
            label="Exchange market"
            name="exchangeMarket"
            rules={[{ required: true }]}
          >
            <Select onChange={setIsShowPassPhraseFieldIfNeeded}>
              {Object.values(ExchangeMarket).map((market) => (
                <Select.Option value={market} key={market}>
                  {market}
                </Select.Option>
              ))}
            </Select>
          </Form.Item>
          <Form.Item label="API key" name="apiKey" rules={[{ required: true }]}>
            <Input.Password />
          </Form.Item>

          <Form.Item
            label="API secret"
            name="secret"
            rules={[{ required: true }]}
          >
            <Input.Password />
          </Form.Item>

          {isShowPassPhraseField && (
            <Form.Item
              label="Password phrase"
              name="passPhrase"
              rules={[{ required: true }]}
            >
              <Input.Password />
            </Form.Item>
          )}

          <Form.Item label="Token" name="token" rules={[{ required: true }]}>
            <Input placeholder="ETH | BTC | USDT | ..." />
          </Form.Item>

          <Form.Item
            label="Network"
            name="network"
            rules={[{ required: true }]}
          >
            <Input placeholder="ERC20 | BSC | AVAXC | MATIC | ARBITRUM | OPTIMISM | APT | ..." />
          </Form.Item>

          <Form.Item>
            <Button type="primary" disabled={!isCORSDisabled} htmlType="submit">
              Send
            </Button>
          </Form.Item>

          {!isCORSDisabled && <CORSUnblock />}
        </Col>

        <Col span={12}>
          <Form.Item
            label={`Amount from ${token ? `(${token})` : ""}`}
            name="amountFrom"
            rules={[{ required: true }]}
          >
            <Input placeholder="0.1" />
          </Form.Item>

          <Form.Item
            label={`Amount to ${token ? `(${token})` : ""}`}
            name="amountTo"
            rules={[{ required: true }]}
          >
            <Input placeholder="0.3" />
          </Form.Item>

          <Form.Item
            label="Delay from (seconds)"
            name="delayFrom"
            rules={[{ required: true }]}
          >
            <Input placeholder="1" />
          </Form.Item>

          <Form.Item
            label="Delay to (seconds)"
            name="delayTo"
            rules={[{ required: true }]}
          >
            <Input placeholder="3" />
          </Form.Item>

          <Form.Item
            label="Wallets"
            name="wallets"
            rules={[{ required: true }]}
          >
            <Input.TextArea
              placeholder={"0x...\n0x...\n0x..."}
              autoSize={{ minRows: 4 }}
            />
          </Form.Item>

          <Form.Item name="isShuffleWallets" valuePropName="checked">
            <Checkbox>Shuffle wallets</Checkbox>
          </Form.Item>
        </Col>
      </Row>
    </Form>
  );
};

const BinanceWithdrawStatus: React.FC<IBinanceWithdrawProps> = ({
  status = "",
  setStatus,
}) => {
  return (
    <>
      <Space direction="vertical" size={25}>
        {status.split("\n").map((status, index) => (
          <Typography.Text key={index}>{status}</Typography.Text>
        ))}
        <Row gutter={10}>
          <Col>
            <Button type="primary" onClick={() => setStatus("")}>
              Close status
            </Button>
          </Col>
          <Col>
            <Button
              type="primary"
              danger
              onClick={() => window.location.reload()}
            >
              Stop
            </Button>
          </Col>
        </Row>
      </Space>
    </>
  );
};

const BinanceWithdraw: React.FC = () => {
  const [status, setStatus] = useState("");

  return status ? (
    <BinanceWithdrawStatus setStatus={setStatus} status={status} />
  ) : (
    <BinanceWithdrawForm setStatus={setStatus} />
  );
};

export default BinanceWithdraw;
