import { WithdrawOneData } from "../types";

export const binanceWithdraw = async (data: WithdrawOneData): Promise<void> => {
  try {
    // @ts-ignore
    const binance = new ccxt.binance({
      apiKey: data.apiKey,
      secret: data.secret,
      enableRateLimit: true,
      options: {
        defaultType: "spot",
      },
    });

    await binance.withdraw(data.token, data.amount, data.wallet, null, {
      network: data.network,
    });

    data.setStatus(
      (prev: string) => `${prev}>>> Success | ${data.wallet} | ${data.amount}\n`
    );
  } catch (error: any) {
    data.setStatus(
      (prev: string) =>
        `${prev}>>> Fail | ${data.wallet} | error: ${error.message}\n`
    );
  }
};
